export {wordList};

export function getWordle() {
    let rand = Math.floor(Math.random() * 10657);
    return wordList[rand];
}

export function getLetterMap() {
    let str = [...'abcdefghijklmnopqrstuvwxyz'];
    let map = new Map();
    for (let i = 0; i < 26; i++) {
        map.set(str[i], 'black');
    }
    return map;
}